import {
  Button,
  CardContent,
  Grid,
  InputAdornment,
  OutlinedInput,
  Paper,
  TextField,
  Typography,
} from '@mui/material';
import {Box} from '@mui/system';
import {useState} from 'react';
import PropTypes from 'prop-types';
import {editRuningCost} from '../../@crema/services/auth/firebase/firebase';
import {useIntl} from 'react-intl';
const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  bgcolor: 'background.paper',
  boxShadow: 24,
};

const EditRunningCost = ({data}) => {
  const {messages} = useIntl();
  const [formData, updateFormData] = useState(data);

  const handleChange = (e) => {
    updateFormData({
      ...formData,

      [e.target.name]: e.target.value.trim(),
    });
  };
  const cheakOutTrigger = (e) => {
    e.preventDefault();

    formData.date = data.date;
    formData.amount = Number(formData.amount);
    editRuningCost(formData, data);
  };
  return (
    <form onSubmit={(e) => cheakOutTrigger(e)}>
      <Box style={style} minWidth={{xs: '90%', sm: '40%'}}>
        <Paper elevation={3}>
          <CardContent xs={{px: 10, pt: 1}} sm={{px: 10, pt: 1}}>
            <Grid container spacing={5}>
              <Grid item xs={12}>
                <Typography
                  mt={2}
                  mb={3}
                  component='h3'
                  variant='h3'
                  sx={{
                    color: (theme) => theme.palette.text.primary,
                    fontSize: {xs: 18, sm: 20},
                  }}
                >
                  Edit Running Cost
                </Typography>
              </Grid>
              <Grid item xs={12} sm={8}>
                <TextField
                  id='outlined-basic'
                  name='name'
                  fullWidth
                  label={messages['mangment.addRunigCost']}
                  variant='outlined'
                  onChange={handleChange}
                  defaultValue={data.name}
                  required
                />
              </Grid>
              <Grid item xs={12} sm={4}>
                <OutlinedInput
                  id='outlined-adornment-amount'
                  name='amount'
                  defaultValue={data.amount}
                  type='Number'
                  fullWidth
                  onChange={handleChange}
                  startAdornment={
                    <InputAdornment position='start'>₪</InputAdornment>
                  }
                />
              </Grid>

              <Grid item xs={12} mt={2} mb={2}>
                <TextField
                  id='outlined-basic'
                  fullWidth
                  name='short_desc'
                  label={['short_desc']}
                  variant='outlined'
                  defaultValue={data.short_desc}
                  size='large'
                  inputProps={{style: {minHeight: 70}}}
                  onChange={handleChange}
                  required
                />
              </Grid>

              <Grid item xs={12}>
                <Button type='submit' variant='contained'>
                  {messages['mangment.update']}
                </Button>
              </Grid>
            </Grid>
          </CardContent>
        </Paper>
      </Box>
    </form>
  );
};
export default EditRunningCost;
EditRunningCost.propTypes = {
  data: PropTypes.object.isRequired,
};
