import React, {useEffect, useState} from 'react';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import Fade from '@mui/material/Fade';
import IconButton from '@mui/material/IconButton';
import MoreVertIcon from '@mui/icons-material/MoreVert';
import Box from '@mui/material/Box';
import EditRunningCost from 'components/RunningCostForm/EditRunningCost';
// import { useNavigate } from 'react-router-dom';
import PropTypes from 'prop-types';
import {useLazyQuery} from '@apollo/client';
import {DELET_RUNINGCOST} from 'query/deletQuerys/deletQuerys';
import {Modal} from '@mui/material';
const RuningActions = ({data}) => {
  const [anchorEl, setAnchorEl] = React.useState(null);
  const open = Boolean(anchorEl);
  const [openEditRunningCostModal, setOpenEditRunningCostModal] =
    useState(false);
  const [deletRuningCost, {data: runing}] = useLazyQuery(DELET_RUNINGCOST);

  // const navigation = useNavigate();
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };
  const handleCloseVariantModal = () => {
    setOpenEditRunningCostModal(false);
  };
  const hanleDelet = (e) => {
    e.preventDefault();
    deletRuningCost({
      variables: {id: data.id, email: data.email},
    });
  };
  useEffect(() => {
    if (runing) {
      console.log(runing);
      alert('DONE Deleted');
    }
  }, [runing]);
  const handleClose = () => {
    setOpenEditRunningCostModal(true);
    setAnchorEl(null);
  };

  return (
    <>
      <Box>
        <IconButton
          aria-controls='alpha-menu'
          aria-haspopup='true'
          onClick={handleClick}
        >
          <MoreVertIcon />
        </IconButton>
        <Menu
          id='alpha-menu'
          anchorEl={anchorEl}
          keepMounted
          open={open}
          onClose={handleClose}
          TransitionComponent={Fade}
        >
          <MenuItem style={{fontSize: 14}} onClick={handleClose}>
            Edit
          </MenuItem>
          <MenuItem style={{fontSize: 14}} onClick={(e) => hanleDelet(e)}>
            Delete
          </MenuItem>
        </Menu>
      </Box>
      <Modal
        aria-labelledby='transition-modal-title'
        aria-describedby='transition-modal-description'
        open={openEditRunningCostModal}
        onClose={handleCloseVariantModal}
        closeAfterTransition
      >
        <EditRunningCost data={data} />
      </Modal>
    </>
  );
};

RuningActions.propTypes = {
  data: PropTypes.object.isRequired,
};
export default RuningActions;
